
.login-form {
    animation: lightSpeedIn 0.5s 1 ease-in-out forwards;
    width: 70%;
    margin: 0 auto;
}
.login-fieldset,
.login-fieldset legend {
    border: 2px solid #CCC;
    border-color: #333;
    border-radius: 0.3em;
    box-shadow: 0 0 2rem 0 rgba(black, 0.1);
    // display: inline-block;
    background: #ccc;
    padding: 0.6em 2.2em;
}

.login-fieldset {
    
    background: $brownColor;
    padding: 3.5vh 4.5vw 3vh 4.5vw;

    //     .login-legend {
    //         display: inline-block;
    //         background: #ccc;
    //         padding: 0.6em 2.2em;
    //         // border-color: #333;
    // }
}



.login-label {
    
    display: block;
    color: #ccc;
}

